<template>
  <div>
    <h1>상세 내용</h1>
    <div>
      <div>
        <strong> name: {{movie.name}}</strong>
        <strong> year: {{movie.year}}</strong>
        <strong> director: {{movie.director}}</strong>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  created: function () {
    var id = this.$route.params.id
    this.$http.get(`/api/movies/${id}`)
    .then((response) => {
      this.movie = response.data[0]
    })
  },
  data: function () {
    return {
      movie: {}
    }
  }
}
</script>